
body {
  background: #28394d url(https://shapeshift.io/images/bg.jpg) no-repeat center center;
}
#app {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
}
#poweredBy {
  text-align: center;
  display: block;
  font-size: 0.75rem;
}
@media only screen and (max-width:767px) {
#app {
    width: 100% !important;
    margin: 0 !important;
    position: static;
}
}



